"use client"

import { useState } from "react"
import ScrollReveal from "./ScrollReveal"

export default function Contact() {
  const [s, setS] = useState({ name: "", email: "", message: "" })
  const [status, setStatus] = useState("")

  async function submit(e) {
    e.preventDefault()
    setStatus("Sending...")

    try {
      const res = await fetch("/api/contact", {
        method: "POST",
        body: JSON.stringify(s),
      })

      if (res.ok) {
        setStatus("Message Sent ✅")
        setS({ name: "", email: "", message: "" })
      } else {
        setStatus("Error ❌")
      }
    } catch {
      setStatus("Error ❌")
    }
  }

  return (
    <section id="contact" className="py-24 bg-[#0a0a0f] text-white">
      <div className="max-w-2xl mx-auto px-6">

        <ScrollReveal>
          <h3 className="text-3xl font-bold mb-8 text-center">Contact Us</h3>
        </ScrollReveal>

        <ScrollReveal delay={0.1}>
          <form
            onSubmit={submit}
            className="
              space-y-5 p-8 rounded-2xl
              bg-white/5 border border-white/10 backdrop-blur-xl
              shadow-xl shadow-black/30
            "
          >
            {/* NAME */}
            <input
              className="
                w-full p-3 rounded-lg bg-black/30 border border-white/10
                text-white placeholder-gray-400 focus:outline-none
                focus:border-green-500 transition
              "
              placeholder="Your Name"
              value={s.name}
              onChange={(e) => setS({ ...s, name: e.target.value })}
              required
            />

            {/* EMAIL */}
            <input
              className="
                w-full p-3 rounded-lg bg-black/30 border border-white/10
                text-white placeholder-gray-400 focus:outline-none
                focus:border-green-500 transition
              "
              placeholder="Your Email"
              type="email"
              value={s.email}
              onChange={(e) => setS({ ...s, email: e.target.value })}
              required
            />

            {/* MESSAGE */}
            <textarea
              className="
                w-full p-3 h-32 rounded-lg bg-black/30 border border-white/10
                text-white placeholder-gray-400 focus:outline-none
                focus:border-green-500 transition
              "
              placeholder="Your Message"
              value={s.message}
              onChange={(e) => setS({ ...s, message: e.target.value })}
              required
            />

            {/* BUTTON + STATUS */}
            <div className="flex items-center gap-4">
              <button
                className="
                  px-6 py-3 font-semibold rounded-lg
                  bg-green-500 text-black 
                  hover:bg-green-400 transition-all
                  shadow-lg hover:shadow-green-500/50
                "
              >
                Send Message
              </button>

              <span className="text-sm text-green-400">{status}</span>
            </div>
          </form>
        </ScrollReveal>

      </div>
    </section>
  )
}
