"use client"

import ScrollReveal from "./ScrollReveal"

export default function Services() {

  const items = [
    {
      title: "Web Development",
      body: "Next.js, React, Node.js, API design",
      img: "/images/services/web.png"
    },
    {
      title: "UI/UX Design",
      body: "Design systems, prototyping, research",
      img: "/images/services/design.png"
    },
    {
      title: "Brand Strategy",
      body: "Identity, messaging, positioning",
      img: "/images/services/brand.png"
    }
  ]

  return (
    <section className="py-24 bg-[#0a0a0f] text-white">
      <h2 className="text-3xl font-bold text-center mb-12">Our Services</h2>

      <div className="max-w-6xl mx-auto grid grid-cols-1 md:grid-cols-3 gap-10 px-6">

        {items.map(item => (
          <ScrollReveal key={item.title}>
            <div className="
              bg-[#111118] border border-white/5 p-6 rounded-2xl shadow-xl 
              hover:shadow-green-500/20 hover:-translate-y-2 
              transition-all duration-300
            ">
              <img src={item.img} className="h-24 mx-auto mb-4" />

              <h3 className="text-xl font-semibold text-center">{item.title}</h3>

              <p className="text-gray-400 text-center mt-2">
                {item.body}
              </p>
            </div>
          </ScrollReveal>
        ))}

      </div>
    </section>
  )
}
