"use client"

import Link from "next/link"
import Image from "next/image"
import ScrollReveal from './ScrollReveal'

const projects = [
  { slug: 'project-alpha', title: 'Project Alpha', img: '/images/project/alpha.jpg' },
  { slug: 'project-beta', title: 'Project Beta', img: '/images/project/beta.jpg' },
  { slug: 'project-gamma', title: 'Project Gamma', img: '/images/project/gamma.jpg' },
]

export default function ProjectsPreview() {
  return (
    <section className="py-24 bg-[#0a0a0f] text-white">
      <ScrollReveal>
        <h2 className="text-3xl font-bold text-center mb-14">
          Selected Projects
        </h2>
      </ScrollReveal>

      <div className="max-w-7xl mx-auto grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-10 px-6">

        {projects.map((p, idx) => (
          <ScrollReveal key={p.slug} delay={idx * 0.1}>
            <Link href={`/projects/${p.slug}`}>

              <div className="
                group cursor-pointer overflow-hidden rounded-2xl 
                bg-[#111118] border border-white/5 
                shadow-xl transition-all duration-500
                hover:-translate-y-2 hover:shadow-green-500/30
              ">
                
                {/* IMAGE */}
                <div className="relative w-full h-56 overflow-hidden">
                  <Image
                    src={p.img}
                    alt={p.title}
                    fill
                    className="
                      object-cover transition-all duration-500 
                      group-hover:scale-110 group-hover:opacity-90
                    "
                  />
                </div>

                {/* CARD BODY */}
                <div className="p-6">

                  <h3 className="text-xl font-semibold mb-1">
                    {p.title}
                  </h3>

                  <p className="text-gray-400 text-sm">
                    View project details →
                  </p>

                </div>

              </div>

            </Link>
          </ScrollReveal>
        ))}

      </div>
    </section>
  )
}
