"use client"

import Image from "next/image"
import Link from "next/link"
import ScrollReveal from "./ScrollReveal"

export default function AboutPreview() {
  return (
    <section className="py-24 bg-[#0a0a0f] text-white">
      <div className="max-w-7xl mx-auto grid md:grid-cols-2 gap-14 items-center px-6">

        {/* TEXT SECTION */}
        <ScrollReveal>
          <div>
            <h2 className="text-3xl font-bold mb-4">About Us</h2>

            <p className="text-gray-300 mb-6 leading-relaxed max-w-lg">
              We are a digital agency passionate about shaping experiences that help brands grow.
              Our team blends creativity, strategy, and engineering to build products users love.
            </p>

            <Link
              href="/about"
              className="
                px-5 py-3 rounded-lg font-medium 
                bg-green-500 text-black transition-all duration-300
                hover:bg-green-400 hover:shadow-lg hover:shadow-green-500/40
              "
            >
              Learn more →
            </Link>
          </div>
        </ScrollReveal>

        {/* IMAGE SECTION */}
        <ScrollReveal delay={0.1}>
          <div
            className="
              relative h-64 w-full rounded-xl overflow-hidden 
              group shadow-lg border border-white/5
            "
          >
            <Image
              src="/images/about/about1.jpg"
              alt="About"
              fill
              className="
                object-cover transition-all duration-500 
                group-hover:scale-110 group-hover:brightness-110
              "
            />

            {/* Glowing green hover aura */}
            <div className="
              absolute inset-0 rounded-xl opacity-0 group-hover:opacity-40
              bg-green-500/20 blur-2xl transition duration-500
            "></div>
          </div>
        </ScrollReveal>

      </div>
    </section>
  )
}
